import Paper from "paper";
import { TweenMax, Bounce } from "gsap";

const PaperLiquide = (canvas, mobile = false) => {
  const paper = new Paper.PaperScope();
  paper.setup(canvas);

  const path = new paper.Path();
  const view = paper.view;
  const tool = new paper.Tool();
  const points = mobile ? 4 : 6;

  let width, height, center;
  let mousePos = view.center;
  let pathHeight = mobile ? mousePos.x : mousePos.y;
  let smooth = true;

  console.log(mobile, window.innerWidth);

  if (mobile) {
    path.fillColor = {
      gradient: {
        stops: [["#82dddc", 0.5], ["#7ab7ea"]]
      },
      origin: view.bounds.leftCenter,
      destination: view.bounds.rightCenter
    };
  } else {
    path.fillColor = {
      gradient: {
        stops: [["#6E85FD", 0.3], "#86F3D2"]
      },
      origin: view.bounds.topCenter,
      destination: view.bounds.bottomCenter
    };
  }

  initializePath();

  function initializePath() {
    if (mobile) {
      center = view.center;
      width = view.size.width / 2;
      height = view.size.height;
      path.segments = [];
      path.add(view.bounds.topRight);
      for (let i = 1; i < points; i++) {
        const point = new paper.Point(center.x, (height / points) * i);
        path.add(point);
      }
      path.add(view.bounds.bottomRight);
    } else {
      center = view.center;
      width = view.size.width;
      height = view.size.height / 2;
      path.segments = [];
      path.add(view.bounds.bottomLeft);
      for (let i = 1; i < points; i++) {
        const point = new paper.Point((width / points) * i, center.y);
        path.add(point);
      }
      path.add(view.bounds.bottomRight);
    }
  }

  let isStart = false;

  view.onFrame = function(event) {
    if (mobile) {
      pathHeight += (center.x - mousePos.x * event.delta - pathHeight) / 10;
      for (let i = 1; i < points; i++) {
        const sinSeed = event.count + (i + (i % 10)) * 100;
        const sinHeight = Math.sin(sinSeed / 200) * pathHeight;
        const yPos = Math.sin(sinSeed / 100) * sinHeight + width;
        // path.segments[i].point.y = yPos;
        TweenMax.to(path.segments[i].point, 0.5, {
          x: yPos,
          ease: Bounce.easeIn
        });
      }
      if (smooth) path.smooth({ type: "continuous" });
    } else {
      if (!isStart) {
        pathHeight += (center.y - mousePos.y * event.delta - pathHeight) / 10;
      } else {
        pathHeight += (center.y - mousePos.y - pathHeight) / 10;
      }
      for (let i = 1; i < points; i++) {
        const sinSeed = event.count + (i + (i % 10)) * 100;
        const sinHeight = Math.sin(sinSeed / 200) * pathHeight;
        const yPos = Math.sin(sinSeed / 100) * sinHeight + height;
        // path.segments[i].point.y = yPos;
        TweenMax.to(path.segments[i].point, 0.5, {
          y: yPos,
          ease: Bounce.easeIn
        });
      }
      if (smooth) path.smooth({ type: "continuous" });
    }
  };

  tool.onMouseMove = function(event) {
    isStart = true;
    mousePos = event.point;
  };

  view.onResize = function(event) {
    initializePath();
  };
};

export default PaperLiquide;
