import React from "react";
import styled, { keyframes } from "styled-components";

import posed from "react-pose";
import Responsive from "react-responsive";

import iconText from "../../img/icon-menu-text.svg";
import media from "../utils/breakPoints";

const Desktop = props => <Responsive {...props} minWidth={1161} />;
const Tablet = props => <Responsive {...props} maxWidth={1160} />;

const PulseAnimation = posed.i({
  pulse: {
    opacity: 0.1,
    scale: 1.5,
    transition: {
      duration: 2000,
      ease: "backOut",
      loop: Infinity
    }
  }
});

const Pulse = styled(PulseAnimation)`
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 1;
  border-radius: 50%;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.15);
  opacity: 0.8;
`;

const Button = styled.button`
  --bgColor: ${props => (props.theme === "black" ? "#3B3D43" : "#fff")};
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  border: none;
  background-color: var(--bgColor);
  border-radius: 50%;
  box-shadow: ${props =>
    props.theme ? "" : "3px 3px 10px rgba(0, 0, 0, 0.15)"};
  z-index: 5;

  ${media.tabletMax`
    width: 35px;
    height: 35px;
  `}

  &:focus {
    outline: none;
  }

  & svg {
    ${media.tabletMax`
      position: relative;
      top: 1px;
      left: 1px;
      transform: scale(0.7);
    `}
  }
`;

const Icon = styled.i`
  width: 14px;
  height: 25px;
  background: url(${iconText}) no-repeat;
`;

const Wrapper = styled.div`
  position: relative;
  width: 44px;
  height: 44px;
  margin-left: 68px;

  ${media.tabletMax`
    width: 35px;
    height: 35px;
  `}
`;

const MenuBtn = () => {
  return (
    <Wrapper>
      <Desktop>
        <Button>
          <Icon />
        </Button>
        <Pulse initialPose="none" pose="pulse" />
      </Desktop>

      <Tablet>
        <Button theme="black">
          <svg
            width="14"
            height="25"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M0 0h1.259l1.683 5.474h.025L4.637 0H5.91v10.339h-1.31V4.05h-.026L3.276 8.495H2.62L1.336 4.05H1.31v6.288H0V0zM9.761 0H14v1.394h-2.929v3.035h2.595v1.394h-2.595v3.035H14v1.48H9.761V0zM0 14.574h1.26l3.313 6.892h.026v-6.892h1.31v10.339H4.676l-3.34-6.709H1.31v6.709H0V14.574zM13.852 22.59c0 .338-.056.653-.167.943-.111.29-.265.547-.462.77a2.139 2.139 0 0 1-.668.508c-.257.126-.531.189-.822.189-.292 0-.566-.063-.822-.189a2.296 2.296 0 0 1-.681-.508 2.648 2.648 0 0 1-.617-1.713v-8.016h1.31v7.87c0 .368.078.64.232.814a.74.74 0 0 0 .578.26.74.74 0 0 0 .578-.26c.154-.175.231-.446.231-.814v-7.87h1.31v8.016z"
              fill="#fff"
            />
          </svg>
          Í
        </Button>
      </Tablet>
    </Wrapper>
  );
};

export default MenuBtn;
