import React, { useState, useEffect } from "react";

import Logo from "./Logo";
import {
  Header,
  Breadcrumbs,
  ButtonCircle,
  Button,
  ButtonOutline,
  ButtonWithAnimation,
  IconPhone,
  LinkTel
} from "./styled";
import MenuBtn from "./MenuBtn";
import { PixiElem, PixiElem1 } from "./pixi";

import Responsive from "react-responsive";

const Desktop = props => <Responsive {...props} minWidth={1161} />;
const Tablet = props => <Responsive {...props} maxWidth={1160} />;
// const Mobile = props => <Responsive {...props} maxWidth={767} />;

const isMobile = () => window.innerWidth <= 1160;

const hasElemPixi = elem =>
  Array.from(elem.children).some(e => (e.nodeName === "CANVAS" ? e : false));

const Nav = props => {
  const [btnIsHover, setBtnIsHover] = useState(false);

  const [addPixi, setAddPixi] = useState(false);

  useEffect(() => {
    // console.log(hasElemPixi(btnOutlineRef));
    if (!isMobile()) {
      if (btnOutlineRef && !hasElemPixi(btnOutlineRef))
        btnOutlineRef.appendChild(PixiElem());

      if (btnFreezeRef && !hasElemPixi(btnFreezeRef))
        btnFreezeRef.appendChild(PixiElem1());

      if (btnPhoneRef && !hasElemPixi(btnPhoneRef))
        btnPhoneRef.appendChild(PixiElem1());
    }
  });

  let btnOutlineRef = null;
  let btnFreezeRef = null;
  let btnPhoneRef = null;

  // TODO: удалить
  // const handleMouseEnter = e => {
  //   if (!addPixi) {
  //     setAddPixi(true);
  //     e.currentTarget.appendChild(PixiElem());
  //   }
  // };

  // const handleMouseLeave = e => {
  //   if (addPixi) {
  //     setAddPixi(false);
  //     const canvas = Array.from(e.currentTarget.children).find(elm =>
  //       elm.nodeName === "CANVAS" ? elm : false
  //     );

  //     if (canvas) e.currentTarget.removeChild(canvas);
  //   }
  // };

  return (
    <Header id="header">
      <Logo rotateDeg={props.logoRotateDeg} />
      <Breadcrumbs />

      <Desktop>
        <ButtonCircle
          ref={ref => (btnPhoneRef = ref)}
          onMouseEnter={e => {
            setBtnIsHover(true);
          }}
          onMouseLeave={e => {
            setBtnIsHover(false);
          }}
          pose={btnIsHover ? "hover" : "init"}
        >
          <IconPhone />
          <LinkTel href={`tel:+7 (4212) 99-99-99`}>+7 (4212) 99-99-99</LinkTel>
        </ButtonCircle>

        <Button ref={ref => (btnFreezeRef = ref)} mLeft="20px">
          <span>Заморозить карту</span>
          <i className="icon-freeze" />
        </Button>
        <Button ref={ref => (btnOutlineRef = ref)} fontSize="14px" mLeft="20px">
          <span>Вступить в клуб</span>
        </Button>
      </Desktop>
      
      <MenuBtn />
    </Header>
  );
};

export default Nav;
